import { Component } from '@angular/core';
import { AppComponent } from '../../app.component';
import { BlockContentComponent, direction } from '../../templates/block-content/block-content.component';
import { CommonModule } from '@angular/common';
import { SheetsService } from '../../sheets.service';

@Component({
  selector: 'app-landing',
  standalone: true,
  imports: [BlockContentComponent, CommonModule],
  templateUrl: './landing.component.html',
  styleUrl: './landing.component.less',
  providers: [BlockContentComponent],
})
export class LandingComponent {
  constructor(
    protected app: AppComponent,
  ) {}

  dir = direction;
  maps = this.app.maps;
  profile = this.app.header;

  protected tracker(i: number, x: any) {
    return x;
  }
}
